.login-wrapper {
    z-index: 0;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 56px;
    background: #eee;
    .login-box {
        width: 400px;
        height: 180px;
        margin: 100px auto;
        padding-top: 20px;
        background: #fff;
        box-shadow: 0 0 8px rgba(0,0,0,.1);
    }
    input {
        display: block;
        width: 200px;
        height: 30px;
        line-height: 30px;
        padding: 0 10px;
        margin: 10px auto;
        color: #777;
    }
    .button {
        width: 220px;
        height: 30px;
        line-height: 30px;
        color: #fff;
        background: #3194d0;
        border-radius: 15px;
        margin: 10px auto;
        text-align: center;
    }
}